import * as JsSIP from "jssip";

const CALL_STATUS = {
  OFFLINE: "offline",
  READY: "ready",
  DIALING: "dialing",
  RINGING: "ringing",
  CONVERSATION: "conversation",
  HANGING: "hanging",
};

const STUN = "stun.l.google.com:19302";
const PORT = 8089;

class Dialer {
  constructor() {
    this.predictive = null;

    this.sipUserAgent;
    this.sipSession;
    this.remoteAudio = null;

    this.callbacks = {};

    this.sipRegistered = false;
    this.userState = "disconnected";
    this.callState = "offline";
    // Register callbacks

    this.eventHandlers = {
      accepted: (e) => {
        this.sipCallingResult({
          status: "Conversation in progress",
        });
      },
      connecting: (e) => {
        this.sipCallingResult({
          status: "Connecting...",
        });
      },
      progress: (e) => {
        this.setCallState(CALL_STATUS.DIALING);
        this._call.progress(e);
      },
      failed: (e) => {
        this.sipCallingResult({
          status: "Failed",
          message: e.message?.data,
          cause: e.cause,
          originator: e.originator,
        });

        this.setCallState(CALL_STATUS.READY);
      },

      ended: (e) => {
        this.callResult = {
          status: "Ended",
          cause: e.cause,
        };
        this.sipCallingResult({
          status: this.predictive ? "  " : "Ended",
          cause: e.cause,
          originator: e.originator,
        });

        this.setCallState(CALL_STATUS.READY);
      },
      confirmed: (e) => {
        this.sipCallingResult({
          status: this.predictive
            ? " ,  "
            : "Conversation in progress",
        });

        this.setStream();

        this.callResult = {
          status: "Confirmed",
        };
      },
    };

    this.options = {
      eventHandlers: this.eventHandlers,
      mediaConstraints: { audio: true, video: false },
    };

    this._call = {
      progress: () => {
        this.setStream();
      },

      start: () => {
        this.setCallState(CALL_STATUS.CONVERSATION);
      },

      end: (state, cause = null) => {
        this.setCallState(CALL_STATUS.READY);
      },

      ring: ({ event, from }) => {
        this.setCallState(CALL_STATUS.RINGING);

        this.sipCallingResult({
          status: " ",
          from: from,
        });
      },
    };
  }

  // TO DO  
  isConnected() {
    // this.callbacks['isConnected']();
    return this.sipUserAgent?.isConnected();
  }

  initOperator(l, p, h) {
    this.createDOM();
    this.connect(l, p, h);
  }

  createDOM() {
    var audio = document.createElement("audio");
    audio.innerHtml = "Your browser does not support the audio element";
    audio.id = "caller_sip_audio_remote";
    document.body.appendChild(audio);

    this.remoteAudio = document.getElementById(audio.id);
  }

  // onConnect(callback) {
  //
  //   this.callbacks.onConnect = callback;
  // }

  onConnect(callback) {
    this.callbacks.onConnect = callback;
  }

  // Callbacks
  on(eventName, callback) {
    this.callbacks[eventName] = callback;
  }

  // TODO other class Event emmiter callbacks
  message(eventName, callback) {
    this.callbacks[eventName] = callback;
  }

  sipConnectionResult(message, event, login) {
    if (
      this.callbacks.connection &&
      typeof this.callbacks.connection === "function"
    ) {
      this.callbacks.connection({
        message,
        login,
        cause: event?.cause,
        status_code: event?.response?.status_code,
        method: event?.response?.method,
      });
    } else {
      // TODO
      // this.callbacks.connection("callback must be a function");
    }
  }

  sipCallingResult(result) {
    if (this.callbacks.call && typeof this.callbacks.call === "function") {
      this.callbacks.call({
        status: result.status || "Status not found",
        message: result.message || "",
        direction: result.direction || "",
        from: result.from || "",
        cause: result.cause || "",
        originator: result.originator || "",
        extended: result || "",
      });
    } else {
      // TO DO
      // this.callbacks.callStatus("callback must be a function");
    }
  }

  answer() {
    // if (this.callState != CALL_STATUS.RINGING) {
    //   callback();
    //   return;
    // }

    if (!this.sipSession) {
      return;
    }

    // Answer call
    this.sipSession.answer(this.options);
  }

  //     
  reject() {
    // if (this.state != CALL_STATUS.RINGING) {
    //   return;
    // }
    this.sipSession?.terminate();
  }

  enterInRoom() {
    // if (this.isConnected) {
    this.call("100");
    // } else {
    // TO DO
    // }
  }

  call(number) {
    // if (this.userState !== SIP_STATUS.REGISTERED) {
    //   console.log("Can not call now");
    //   return;
    // }

    if (this.predictive && number !== "100") {
      return this.message("unavailable service");
    }

    this.sipSession = this.sipUserAgent?.call(number, this.options);
  }

  setStream() {
    let outgoing = this.sipSession.direction === "outgoing";
    let remote_stream = this.sipSession.connection?.getRemoteStreams()[0];
    this.remoteAudio.srcObject = remote_stream;
    this.remoteAudio.play();
  }

  setSipState(state) {
    this.userState = state;
  }
  setCallState(state) {
    this.callState = state;
  }

  disconnect() {
    this.sipUserAgent?.stop();
  }

  terminate() {
    if (this.sipSession) this.sipSession?.terminate();
    if (this.sipUserAgent) this.sipUserAgent?.terminateSessions();
  }

  // PREDICTIVE

  offline() {
    this.sipUserAgent?.stop();
  }

  connect(login, password, host) {
    // TODO
    if (arguments.length < 3) return this.message("3 arguments required");
    // TODO
    if (!this.predictive) {
      this.createDOM();

      let sipLogin = `sip:${login}@${host}`;
      let server = `wss://${host}:${PORT}/ws`;
      let socket = new JsSIP.WebSocketInterface(server);
      // let socket = WebSocketInterface(server);
      let configuration = {
        sockets: [socket],
        uri: sipLogin,
        password: password,
        stun_servers: [STUN],
        trace_sip: true,
        hack_via_ws: true,
        session_timers_refresh_method: "invite",
      };

      // this.sipUserAgent = UA(configuration);
      this.sipUserAgent = new JsSIP.UA(configuration);
      this.sipUserAgent.start();

      this.sipUserAgent.once("connected", (e) => {
        this.setCallState(CALL_STATUS.READY);
        this.sipConnectionResult("connected", e, login);
      });
      this.sipUserAgent.once("disconnected", (e) => {
        this.setCallState(CALL_STATUS.OFFLINE);

        this.sipConnectionResult("disconnected", e, login);
        this.sipCallingResult({ status: "Offline" });
      });
      this.sipUserAgent.once("registered", (e) => {
        this.setCallState(CALL_STATUS.READY);
        this.sipCallingResult({ status: "Ready" });
        this.sipConnectionResult("registered", e, login);
      });
      this.sipUserAgent.once("unregister", (e) => {
        this.setCallState(CALL_STATUS.OFFLINE);
        this.sipCallingResult({ status: "offline" });
        this.terminate();
        this.sipConnectionResult("unregister", e, login);
      });
      this.sipUserAgent.once("registrationFailed", (e) => {
        this.setCallState(CALL_STATUS.OFFLINE);
        this.sipCallingResult({ status: "offline" });
        this.terminate();
        this.sipConnectionResult("registrationFailed", e, login);
      });

      this.sipUserAgent.on("call", (e) => {
        this.setCallState(CALL_STATUS.DIALING);
        this.sipCallingResult({ status: " " });
      });

      this.sipUserAgent.on("progress", (e) => {
        this.setCallState(CALL_STATUS.DIALING);
        this.sipCallingResult({ status: "Conversation" });
      });

      this.sipUserAgent.on("newMessage", (e) => {
        this.sipCallingResult({
          status: "Conversation in progress",
          direction: e._direction,
          from: e.request.body,
        });
      });

      this.sipUserAgent.on("reject", (e) => {
        this.sipCallingResult({
          status: "reject",
          direction: e._direction,
          from: e.request.body,
        });
      });

      this.sipUserAgent.on("destroyMessage", (e) => {
        this.sipCallingResult({
          status: "destroyMessage",
          direction: e._direction,
          from: e.request?.body,
        });
      });

      this.sipUserAgent.on("sipEvent", (e) => {
        this.sipCallingResult({
          status: "sipEvent",
          direction: e._direction,
          from: e.request?.body,
        });
      });

      this.sipUserAgent.on("newDialog", (e) => {
        this.sipCallingResult({
          status: "newDialog",
          direction: e._direction,
          from: e.request.body,
        });
      });

      this.sipUserAgent.on("destroyDialog", (e) => {
        this.sipCallingResult({
          status: "destroyDialog",
          direction: e._direction,
          from: e.request.body,
        });
      });

      this.sipUserAgent.on("newRTCSession", (data) => {
        this.sipSession = data.session;

        this.sipSession.on("update", (e) => {});

        this.sipSession.on("newInfo", (e) => {});
        this.sipSession.on("reinvite", (e) => {});

        this.sipSession.on("getusermediafailed", (e) => {});

        if (this.sipSession.direction === "incoming") {
          for (var f in this.eventHandlers) {
            this.sipSession.on(f, this.eventHandlers[f]);
          }

          this.sipCallingResult({
            status: "Call",
            direction: data.session._direction,
            from: data.request.from._uri._user,
          });

          // this.sipSession.on("addstream", function (e) {
          //   this.remoteAudio.src = window.URL.createObjectURL(e.stream);
          //   this.remoteAudio.play();
          // });
        } else {
          this.sipCallingResult({
            status: " ",
            direction: data.session._direction,
          });
        }
      });
    } else {
      return this.message("unavailable service");
    }
  }

  online(login, password, host) {
    // TODO
    if (arguments.length < 3) return this.message("3 arguments required");
    // TODO
    if (!this.predictive) {
      return this.message("unavailable service");
    }

    if (this.predictive) {
      this.createDOM();

      let sipLogin = `sip:${login}@${host}`;
      let server = `wss://${host}:${PORT}/ws`;
      let socket = new JsSIP.WebSocketInterface(server);
      // let socket = WebSocketInterface(server);
      let configuration = {
        sockets: [socket],
        uri: sipLogin,
        password: password,
        stun_servers: [STUN],
        trace_sip: true,
        hack_via_ws: true,
        session_timers_refresh_method: "invite",
      };

      this.sipUserAgent = new JsSIP.UA(configuration);
      // this.sipUserAgent = UA(configuration);
      this.sipUserAgent.start();

      this.sipUserAgent.once("connected", (e) => {
        this.setCallState(CALL_STATUS.READY);
        this.sipConnectionResult("connected", e, login);
      });
      this.sipUserAgent.once("disconnected", (e) => {
        this.setCallState(CALL_STATUS.OFFLINE);

        this.sipConnectionResult("disconnected", e, login);
        this.sipCallingResult({ status: "Offline" });
      });
      this.sipUserAgent.once("registered", (e) => {
        this.setCallState(CALL_STATUS.READY);
        this.sipCallingResult({ status: "Ready" });
        this.sipConnectionResult("registered", e, login);

        // CALL TO PREDICTIVE
        this.call("100");
      });
      this.sipUserAgent.once("unregister", (e) => {
        this.setCallState(CALL_STATUS.OFFLINE);
        this.sipCallingResult({ status: "offline" });
        this.sipConnectionResult("unregister", e, login);
      });
      this.sipUserAgent.once("registrationFailed", (e) => {
        this.setCallState(CALL_STATUS.OFFLINE);
        this.sipCallingResult({ status: "offline" });
        this.sipConnectionResult("registrationFailed", e, login);
      });

      this.sipUserAgent.on("call", (e) => {
        this.setCallState(CALL_STATUS.DIALING);
        this.sipCallingResult({ status: " " });
      });

      this.sipUserAgent.on("newMessage", (e) => {
        this.sipCallingResult({
          status: "Conversation in progress",
          direction: e._direction,
          from: e.request.body,
        });
      });

      this.sipUserAgent.on("reject", (e) => {
        this.sipCallingResult({
          status: "reject",
          direction: e._direction,
          from: e.request.body,
        });
      });

      this.sipUserAgent.on("destroyMessage", (e) => {
        this.sipCallingResult({
          status: "destroyMessage",
          direction: e._direction,
          from: e.request.body,
        });
      });

      this.sipUserAgent.on("sipEvent", (e) => {
        this.sipCallingResult({
          status: "sipEvent",
          direction: e._direction,
          from: e.request.body,
        });
      });

      this.sipUserAgent.on("newDialog", (e) => {
        this.sipCallingResult({
          status: "newDialog",
          direction: e._direction,
          from: e.request.body,
        });
      });

      this.sipUserAgent.on("destroyDialog", (e) => {
        this.sipCallingResult({
          status: "destroyDialog",
          direction: e._direction,
          from: e.request.body,
        });
      });

      this.sipUserAgent.on("newRTCSession", (data) => {
        this.sipSession = data.session;

        this.sipSession.on("update", (e) => {});

        this.sipSession.on("newInfo", (e) => {});
        this.sipSession.on("reinvite", (e) => {});

        this.sipSession.on("getusermediafailed", (e) => {});

        if (this.sipSession.direction === "incoming") {
          for (var f in this.eventHandlers) {
            this.sipSession.on(f, this.eventHandlers[f]);
          }

          this.sipCallingResult({
            status: " ??",
            direction: data.session._direction,
            from: data.request.from._uri._user,
          });

          // this.sipSession.on("addstream", function (e) {
          //   this.remoteAudio.src = window.URL.createObjectURL(e.stream);
          //   this.remoteAudio.play();
          // });
        } else {
          this.sipCallingResult({
            status: " ",
            direction: data.session._direction,
          });
        }
      });
    }
  }
}

// export default new Dialer();

window.zorra = window.dialer = new Dialer();
export default Dialer;
